<template>
  <div>
    <nav-header  :userFlag="userFlag"></nav-header>
    <router-view></router-view>
  </div>
</template>
<script>
import navHeader from "../components/navHeader";
export default {
  name: "Home",
  components: {
    navHeader,
  },
  data() {
    return {
      userFlag: 0,
    };
  },
  watch: {
    // 监听路由变化（使用户信息能够始终保存）
    '$route':{
      handler() {
        this.userFlag =  Math.random();
      },
      immediate: true
    }
  },
};
</script>
